import React, { useState } from "react"
import "dayjs/locale/ru"
import { Box, Stack, TextField, Typography } from "@mui/material"
import { DataGrid } from "@mui/x-data-grid"
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs"
import { DesktopDatePicker, LocalizationProvider } from "@mui/x-date-pickers"
import { useGetMatches } from "../../app/repository"

export default function Matches() {
  const { data, status } = useGetMatches()

  const [dateFrom, setDateFrom] = useState(
    new Date(
      data != null ? Date.parse(data.matches[0].utcDate) : "1995-12-17T03:24:00"
    )
  )
  const [dateTo, setDateTo] = useState(
    new Date(
      data != null
        ? Date.parse(data.matches[data.matches.length - 1].utcDate)
        : "1995-12-17T03:24:00"
    )
  )

  if (status === "loading") {
    return <div>Loading...</div>
  }

  if (status === "error") {
    return <div>Error</div>
  }

  const columns = [
    {
      field: "date",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: ""
    },
    {
      field: "time",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: ""
    },
    {
      field: "status",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: " "
    },
    {
      field: "homeTeam",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: " "
    },
    {
      field: "awayTeam",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: " "
    },
    {
      field: "scoreFullTime",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: " "
    },
    {
      field: "scoreExtraTime",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: " "
    },
    {
      field: "scorePenalties",
      flex: 1,
      align: "center",
      headerAlign: "center",
      headerName: ""
    }
  ]

  const rows = data.matches.map((match, index) => {
    const dateTime = new Date(Date.parse(match.utcDate))
    const date = dateTime.toLocaleDateString("ru-RU", {
      year: "numeric",
      month: "numeric",
      day: "numeric"
    })
    const time = dateTime.toLocaleTimeString("ru-RU", {
      hour: "2-digit",
      minute: "2-digit"
    })

    let matchStatus = ""
    switch (match.status) {
      case "SCHEDULED":
        matchStatus = ""
        break
      case "LIVE":
        matchStatus = "  "
        break
      case "IN_PLAY":
        matchStatus = " "
        break
      case "PAUSED":
        matchStatus = ""
        break
      case "FINISHED":
        matchStatus = ""
        break
      case "POSTPONED":
        matchStatus = ""
        break
      case "SUSPENDED":
        matchStatus = ""
        break
      case "CANCELED":
        matchStatus = ""
        break
      default:
        break
    }

    let scoreFullTime = ""
    if (match.score.fullTime.homeTeam != null) {
      scoreFullTime = match.score.fullTime.homeTeam
        .toString()
        .concat(":", match.score.fullTime.awayTeam)
    }

    let scoreExtraTime = ""
    if (match.score.extraTime.homeTeam != null) {
      scoreExtraTime = match.score.extraTime.homeTeam
        .toString()
        .concat(":", match.score.extraTime.awayTeam)
    }

    let scorePenalties = ""
    if (match.score.penalties.homeTeam != null) {
      scorePenalties = match.score.penalties.homeTeam
        .toString()
        .concat(":", match.score.penalties.awayTeam)
    }

    return {
      id: index,
      date,
      time,
      status: matchStatus,
      homeTeam: match.homeTeam.name,
      awayTeam: match.awayTeam.name,
      scoreFullTime,
      scoreExtraTime,
      scorePenalties
    }
  })

  return (
    <Stack spacing={2} margin={4}>
      <Typography variant="h5"></Typography>
      <Stack direction="row" alignItems="center" spacing={2}>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
          <Typography variant="p"></Typography>
          <DesktopDatePicker
            size="small"
            value={dateFrom}
            onChange={setDateFrom}
            renderInput={(params) => <TextField {...params} />}
          />
          <Typography variant="p"></Typography>
          <DesktopDatePicker
            size="small"
            value={dateTo}
            onChange={setDateTo}
            renderInput={(params) => <TextField {...params} />}
          />
        </LocalizationProvider>
      </Stack>
      <Box sx={{ height: 400, width: "100%" }}>
        <DataGrid
          columns={columns}
          rows={rows}
          pageSize={5}
          rowsPerPageOptions={[5]}
        />
      </Box>
    </Stack>
  )
}
